body {
    margin: 0;
    padding: 0;
}

a {
    text-decoration: none !important;
}

.spacer {
    margin-top: 10em;
    margin-bottom: 10em;
}

.skill-space {
    margin-top: 10vh;
    margin-bottom: 10vh;
}

.skill-div {
    padding-top: 20vh;
    padding-bottom: 20vh;
}


/*HEADER*/

h1 {
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}

h3 {
    font-family: 'Lora', serif;
    font-style: italic;
}

h4 {
    font-family: 'Lora', serif;
    font-style: italic;
}

.header {
    background-color: #4AB6C4;
    margin-top: -100px;
    height: 90vh;
}

.bars {
    padding: 20px;
    display: block;
}

.nav-custom {
    background-color: #4AB6C4;
    border: none;
}

.nav-custom a {
    list-style: none;
    font-family: 'Montserrat', sans-serif;
    color: white;
    text-align: right;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}

.nav-custom hover {
    color: #95989A;
}

.nav-right {
    display: block;
}

.menu-enter {
    max-height: 0px;
    -webkit-transition: max-height 1s ease;
    overflow: hidden;
}

.menu-enter.menu-enter-active {
    height: auto;
    max-height: 100px;
}

.menu-leave {
    max-height: 100px;
    -webkit-transition: max-height 1s ease;
}

.menu-leave.menu-leave-active {
    overflow: hidden;
    max-height: 0px;
}

ul {
    /*width: 100%;*/
    /*background-color:white;*/
    text-decoration: none;
}

.menu-bk {
    height: 100vh;
    width: 100%;
    background-color: white;
    z-index: 9999;
}

.name-header {
    padding-top: 40vh;
}

.connect-header {
    margin-top: 30vh;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
    display: inline-block;
    padding-bottom: 1vh;
}

.connect-header:hover {
    -webkit-transform: skew(-10deg);
    -ms-transform: skew(-10deg);
        transform: skew(-10deg);
    color: white;
}

.header-inner {
    padding-top: 10px;
}

.name-bottom {
    padding-bottom: 10vh;
}

.white {
    color: white;
}

.angle-right {
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 0px;
}


/*EXPLORE*/

.grid {
    position: relative;
    height: 1200px;
}


/*SKILLS*/

.skills {
    list-style: none;
    font-family: 'Lora', serif;
    color: #95989A;
    font-size: 1.5em;
}

.gray {
    color: #95989A;
}


/*CONNECT*/

.connect {
    font-family: 'Lora', serif;
    font-style: italic;
    color: #95989A;
    display: inline-block;
    font-size: 1.5em;
    background-color: transparent;
    border: none;
    display: inline-block;
}

.skew {
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}

.skew:hover {
    -webkit-transform: skew(-10deg);
    -ms-transform: skew(-10deg);
        transform: skew(-10deg);
}


