.side {
    border-left: .5px solid #95989A;
}

.form-group {
    padding-bottom: 10px;
    border-color: #95989A;
    border-width: .5px;
    font-family: 'Lora', serif;
    font-style: italic;
}

.aboutp {
    margin-top: 20px;
    color: #95989A;
    font-family: 'Lora', serif;
}

.wrapperImage {
    position: relative;
    width: 50%;
}

.wrapperImage:hover .image {
    opacity: 0.3;
}

.wrapperImage:hover .middle {
    opacity: 1;
}

.middle {
    -webkit-transition: .5s ease;
    -o-transition: .5s ease;
    transition: .5s ease;
    opacity: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    -ms-transform: translate(-50%, -50%)
}

.text {
    background-color: #4CAF50;
    color: white;
    font-size: 16px;
    padding: 16px 32px;
}

