.footer-icons {
    padding-right: .5em;
    padding-left: .5em;
    -webkit-transition: .4s;
    -o-transition: .4s;
    transition: .4s;
    color: white;
    text-decoration: none !important;
}

.footer-icons a {
    -webkit-text-decoration-line: none !important;
            text-decoration-line: none !important;
    text-decoration: none !important;
}

.footer-icons:hover {
    -webkit-transform: scale(1.2);
        -ms-transform: scale(1.2);
            transform: scale(1.2);
